import React, {memo, useState} from "react";
import {ClassNameElement} from "../../../../../../components/types";
import classNames from "classnames";
import {Tabs} from "../../../../../../components/Elements/Tabs";
import {LineChart} from "../../../../../../components/LineChart";
import {CHART_DATA_BUTANE, CHART_DATA_PROPANE} from "../../../../config/prices/LineChartPricesTrade";

interface LineChartPricesTradeProps extends ClassNameElement {
}

const tooltipVals = ['2020', ' ', ' '];

const LineChartPricesTrade = memo(({className}: LineChartPricesTradeProps) => {
  const [activeTab, setActiveTab] = useState<number>(0);

  return (<div className={classNames("lineChartPricesTrade", className)}>
    <Tabs
      tabs={
        [
          {
            name: tooltipVals[1],
          },
          {
            name: tooltipVals[2],
          }
        ]
      }
      active={activeTab}
      setActive={setActiveTab}
    >
      <LineChart
        activeTab={0}
        desc={'          2014 .   -  -  ().         ():  , 622,5  (),  .'}
        subtext={activeTab === 0 ? ' . ' : './. . '}
        chartData={activeTab === 0 ? CHART_DATA_PROPANE : CHART_DATA_BUTANE}
        tooltipVals={[]}
        measurement={activeTab === 0 ? ' . ' : './. . '}
      />
    </Tabs>
  </div>)
});

export {LineChartPricesTrade};
