import React, {memo, useState} from "react";
import {PieChart} from "../../../../../../components/PieChart";
import {ClassNameElement} from "../../../../../../components/types";
import classNames from "classnames";
import { PIE_CHART_MINING } from "../../../../config/mining/PieChartMining";

interface PieChartMiningProps extends ClassNameElement {

}

export interface PieChartDataProps {
  value: number,
  name: string,
  shift: string,
  selected?: boolean,
  itemStyle: { color: string },
}

const chartData: PieChartDataProps[][] = [
  [
    {value: PIE_CHART_MINING[0].value, name: PIE_CHART_MINING[0].name, shift: PIE_CHART_MINING[0].shift, selected: true, itemStyle: {color: PIE_CHART_MINING[0].color}},
    {value: PIE_CHART_MINING[1].value, name: PIE_CHART_MINING[1].name, shift: PIE_CHART_MINING[1].shift, itemStyle: {color: PIE_CHART_MINING[1].color}},
  ],
  [
    {value: PIE_CHART_MINING[0].value, name: PIE_CHART_MINING[0].name, shift: PIE_CHART_MINING[0].shift, itemStyle: {color: PIE_CHART_MINING[0].color}},
    {value: PIE_CHART_MINING[1].value, name: PIE_CHART_MINING[1].name, shift: PIE_CHART_MINING[1].shift, selected: true, itemStyle: {color: PIE_CHART_MINING[1].color}},
  ],
];

const PieChartMining = memo(({className}: PieChartMiningProps) => {
  const [active, setActive] = useState(0);

  return (
    <div className={classNames('pieChartMining', className)}>
      <PieChart
        active={active}
        title='  '
        desc='  ,          +2,1 ..  2019 .      +0,5 ..               -0,2 ..  2019 .'
        setActive={setActive}
        chartData={chartData}
        optionsStat={{
          type: 'pie',
          selectedMode: 'single',
          selectedOffset: 0,
          select: {
            itemStyle: {
              borderWidth: 8,
              borderRadius: 0,
              borderCap: 'square',
              borderColor: chartData[active][active].itemStyle.color,
              borderStyle: 'solid',
            },
          },
          radius: ['60%', '75%'],
          avoidLabelOverlap: false,
          label: {
            show: false,
          },
          labelLine: {
            show: false
          },
        }}
      />
    </div>
  )
});

export {PieChartMining};